import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { UtilsProvider } from '../../providers/utils/utils';

/**
 * Generated class for the EstadocuentaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-estadocuenta',
  templateUrl: 'estadocuenta.html',
})
export class EstadocuentaPage {

  datos: any;
  objeto: any;
  prueba: string;
  idioms: any[] = [];
  x : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, private translateService: TranslateService,
              public utils: UtilsProvider) {
   
    this.getData();
    this.idioms = [
      {
        value: 'es_co',
        label: 'EspaÃ±ol',
      },
      {
        value: 'en_us',
        label: 'Ingles'
      }
    ];

    this.objeto = [
      {
       saldoCA: '*7548',
       saldoD: '$ 67.562.02',
       n: '825-256156-47',
       canje: '$ 0.0',
       total: '$ 30.000'
      }
    ]
  this.datos= JSON.parse(JSON.stringify(this.objeto));
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EstadocuentaPage');
  }

   //Tabla 1
   mostrar1() {
    document.getElementById("caja").style.display = "block";
  }

  ocultar1() {
    document.getElementById("caja").style.display = "none";
  }

  buscar() {

    const caja = document.getElementById("caja");

    if (caja.style.display == "none") {
      this.mostrar1();
    }
    else {
      this.ocultar1();
    }
  }

  getData(){
    var tmp: string;
    this.storage.get('name').then((val) => {
      console.log('esta en otra pantalla ', val);     
     this.prueba = val;
    }); 
  }

  lenguague(){
    this.x = this.storage.get('lenguaje').then((val) => {
       console.log('Esta en root', val);
     });
     this.translateService.use(this.x);
   }

   logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }


}



